/* 1. GLOBAL STYLES */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
}

/* 2. TYPOGRAPHY */

.logo {
    font-family: 'Kaushan Script', cursive;
}

/* 3. LAYOUT */
/*    3.1 REUSABLES */

.outer-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.inner-container {
    margin: 0 20px;
    flex-basis: 1400px;
    flex-shrink: 1;
    flex-grow: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search-button {
    background-color: cadetblue;
    font-weight: bold;
    padding: 5px;
    border: 2px darkgreen outset;
    border-radius: 5px;
}

